import {authAPI, LoginParamsType} from "../../../api/api";
import {Dispatch} from "redux";
import {ProfileActionsType, setUserData} from "../../profile/profile-reducer";
import {setAppError, SetAppErrorActionType} from "../../app/app-reducer";
import {AppThunk} from "../../store";

export enum LOGIN_ACTIONS_TYPE {
    SET_IS_LOGGED_IN = 'SET_IS_LOGGED_IN',
}

const initialState = {
    isLoggedIn: false
}
type InitialStateType = typeof initialState

export const loginReducer = (state: InitialStateType = initialState, action: LoginActionsType): InitialStateType => {
    switch (action.type) {
        case LOGIN_ACTIONS_TYPE.SET_IS_LOGGED_IN:
            return {...state, isLoggedIn: action.isLoggedIn}
        default:
            return state
    }
}

export const setIsLoggedIn = (isLoggedIn: boolean) =>
    ({type: LOGIN_ACTIONS_TYPE.SET_IS_LOGGED_IN, isLoggedIn} as const)

//THUNKS
export const loginTC = (data: LoginParamsType): AppThunk => (dispatch: Dispatch<LoginActionsType>) => {
    authAPI.login(data)
        .then((res) => {
            dispatch(setUserData(res.data))
            dispatch(setIsLoggedIn(true))
        })
        .catch((e) => {
            console.log('Error: ', {...e})
            const error = e.response ? e.response.data.error : (e.message + ', more details in the console')
            dispatch(setAppError(error))
        })
}
export const logoutTC = () => (dispatch: Dispatch<LoginActionsType>) => {
//dispatch(IsFetching)
    authAPI.logout()
        .then(res => {
            if (res.info) {
                dispatch(setIsLoggedIn(false))
//dispatch(IsFetching)
            } else {
                dispatch(setAppError('Some error occurred during logout'))
            }
        })
        .catch((error) => {
            dispatch(setAppError(error))
        })
}


// ActionsType  LoginActionsType
export type LoginActionsType = ReturnType<typeof setIsLoggedIn>
    | ProfileActionsType
    | SetAppErrorActionType



